import Vue from 'vue'
import Router from 'vue-router'
import Home from 'components/home.vue'
import Blank from 'components/blank.vue'
import WebShow from 'components/web-show/web-show.vue'
import UEditor from 'components/practice/ueditor.vue'
import Table from 'components/practice/table.vue'
import Echarts from 'components/practice/echarts.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: '',
      redirect: '/website',
      component: Home,
      children: [
        {
          path: 'website',
          name: '',
          redirect: '/website/javascript',
          component: Blank,
          children: [
            {
              path: ':className',
              name: 'web',
              component: WebShow
            }
          ]
        },
        {
          path: 'practice',
          name: '',
          redirect: '/practice/table',
          component: Blank,
          children: [
            {
              path: 'ueditor',
              name: 'ueditor',
              component: UEditor
            },
            {
              path: 'table',
              name: 'table',
              component: Table
            },
            {
              path: 'echarts',
              name: 'echarts',
              component: Echarts
            }
          ]
        }

      ]
    }
  ]
})

export default router



