<template>
  <div id="echarts"  style="width: 600px;height:400px;"></div>
</template>

<script>
import echarts from 'echarts'
export default {
  name: 'echarts',
  data () {
    return {
      echarts: null
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      this.echarts = echarts.init(document.getElementById('echarts'))
      this.echarts.setOption({
        title: {
          text: 'ECharts '
        },
        tooltip: {},
        xAxis: {
          data: ['', '', '', '', '', '']
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      })
    }
  }
}
</script>



